import React, { Component } from "react";

//import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import { Form, Input, Modal } from "antd";
import getLanguage from "../../../App/MyLanguange";

// const { Option } = Select;
// const { TextArea } = Input;

class ModalUpdateContact extends Component {
  componentDidUpdate(prevProps) {
    const { contact_data } = this.props;
    const { setFieldsValue } = this.props.form;

    if (prevProps.contact_data !== contact_data) {
      setFieldsValue({
        customer_contact_name: contact_data.customer_contact_name,
        job_title: contact_data.job_title,
        phone: contact_data.phone,
        email: contact_data.email
      });
    }
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        values.id_customer_contact = this.props.contact_data.id_customer_contact;
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator } = this.props.form;
    const { loading } = this.props;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="updateContact" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="40%"
        okText={getLanguage({ id: "button.submit" })}
        cancelText={getLanguage({ id: "button.cancel" })}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Form.Item
            {...formItemLayout}
            label={<IntlMessages id="customerContacts.add.name" />}
          >
            {getFieldDecorator("customer_contact_name", {
              rules: [
                {
                  required: true,
                  message: <IntlMessages id="required" />
                },
                {
                  max: 50,
                  message: <IntlMessages id="maxLength50" />
                }
              ]
            })(
              <Input
              //autosize={{ minRows: 2 }}
              //disabled={used ? true : false}
              />
            )}
          </Form.Item>

          <Form.Item
            {...formItemLayout}
            label={<IntlMessages id="customerContacts.add.jobTitle" />}
          >
            {getFieldDecorator("job_title", {
              rules: [
                {
                  required: true,
                  message: <IntlMessages id="required" />
                },
                {
                  max: 50,
                  message: <IntlMessages id="maxLength50" />
                }
              ]
            })(
              <Input
              //autosize={{ minRows: 2 }}
              //disabled={used ? true : false}
              />
            )}
          </Form.Item>
          <Form.Item {...formItemLayout} label={<IntlMessages id="phone" />}>
            {getFieldDecorator("phone", {
              rules: [
                {
                  required: true,
                  message: <IntlMessages id="required" />
                },
                {
                  max: 15,
                  message: <IntlMessages id="maxLength15" />
                },
                {
                  pattern: "^[0-9]*$",
                  message: <IntlMessages id="invalidPhone" />
                }
              ]
            })(
              <Input
              //autosize={{ minRows: 2 }}
              //disabled={used ? true : false}
              />
            )}
          </Form.Item>
          <Form.Item {...formItemLayout} label={<IntlMessages id="email" />}>
            {getFieldDecorator("email", {
              rules: [
                {
                  type: "email",
                  message: <IntlMessages id="invalidEmail" />
                }
              ]
            })(<Input allowClear={true} />)}
          </Form.Item>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalUpdateContact = Form.create({ name: "modal_update_contact" })(
  ModalUpdateContact
);

export default WrappedModalUpdateContact;
