import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import ModalAddAddress from "./modalAddAddress";
import ModalUpdateAddress from "./modalUpdateAddress";

import { Tooltip, Spin, Col } from "antd";

import Popconfirms from "../../../../components/feedback/popconfirm";
// import LayoutWrapper from "../../../../components/utility/layoutWrapper";
// import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/customer/customer/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange"

// const Panel = Collapse.Panel;
// const Option = Select.Option;
// const Search = Input.Search;

class CustomerAddress extends Component {

  state = {
    visibleAdd: false,
    visibleUpdate: false,
    address_data: null,
  }


  handleModalAdd = () => {
    this.setState({
      visibleAdd: !this.state.visibleAdd
    })
  }

  handleModalUpdate = row => {
    this.state.visibleUpdate
      ? this.setState({ visibleUpdate: false, address_data: {} })
      : this.setState({ visibleUpdate: true, address_data: row });
  }

  handleDelete = (id) => {
    let values = {};
    values.id_customer_address = id;
    values.is_active = 0;
    this.props.handleSubmitUpdateAddress(values)
  }

  listAction = {
    title: "",
    dataIndex: "id_customer_address",
    rowKey: "id_customer_address",
    width: "10%",
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <React.Fragment>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Button
            size="small"
            className="invoiceEdtBtn"
            onClick={() => this.handleModalUpdate(row)}
          >
            <i className="ion-android-create" />
          </Button>
        </Tooltip>
        <Popconfirms
          title={<IntlMessages id="confirmDelete" />}
          onConfirm={this.handleDelete.bind(this, text)}
          okText={<IntlMessages id="yes" />}
          cancelText={<IntlMessages id="no" />}
        >
          <Tooltip
            title={<IntlMessages id="delete" />}
          >
            <Button
              className="invoiceDltBtn"
              size="small"
            //style={{ marginRight:"10px" }}

            >
              <i className="ion-trash-b" />
            </Button>
          </Tooltip>
        </Popconfirms>
      </React.Fragment>
    )
  };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "addressName" }),
        dataIndex: "customer_address_name",
        rowKey: "customer_address_name",
        width: "10%",
        fixed: "left",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "address" }),
        dataIndex: "address",
        rowKey: "address",
        width: "20%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "district" }),
        dataIndex: "district",
        rowKey: "district",
        width: "10%",
        render: (text, row) => {
          if (text !== null) {
            return <span>{text.district_name}</span>
          }
          else {
            return <span></span>
          }

        }
      },
      {
        title: getLanguage({ id: "zipCode" }),
        dataIndex: "zip_code",
        rowKey: "zip_code",
        width: "5%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "city" }),
        dataIndex: "city",
        rowKey: "city",
        width: "10%",
        render: (text, row) => {
          if (text !== null) {
            return <span>{text.city_type + " " + text.city_name}</span>
          }
          else {
            return <span></span>
          }
        }
      },
      {
        title: getLanguage({ id: "province" }),
        dataIndex: "province",
        rowKey: "province",
        width: "10%",
        render: (text, row) => {
          if (text !== null) {
            return <span>{text.province_name}</span>
          }
          else {
            return <span></span>
          }
        }
      },
      {
        title: getLanguage({ id: "country" }),
        dataIndex: "country",
        rowKey: "country",
        width: "10%",
        render: (text, row) => {
          if (text !== null) {
            return <span>{text.country_name}</span>
          }
          else {
            return <span></span>
          }
        }
      },
      {
        title: getLanguage({ id: "phone1" }),
        dataIndex: "phone1",
        rowKey: "phone1",
        width: "5%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "phone2" }),
        dataIndex: "phone2",
        rowKey: "phone2",
        width: "5%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "fax" }),
        dataIndex: "fax_number",
        rowKey: "fax_number",
        width: "10%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "email" }),
        dataIndex: "email",
        rowKey: "email",
        width: "10%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "website" }),
        dataIndex: "website",
        rowKey: "website",
        width: "10%",
        render: text => <span>{text}</span>
      },
    ];
    //console.log(this.props.customer);
    // console.log(this.state.filter);

    // Data setup
    const { customer, loading, loadingSubmit } = this.props.customer;
    //console.log(customer)
    const customer_addresses = customer !== null ? customer.customer_addresses : [];
    //console.log(this.props.customer); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (
      permissions.includes("Update Customer") &&
      columns.length === 12
    ) {
      columns.push(this.listAction);
    }

    Object.keys(customer_addresses).map((customer_address, index) => {
      return dataSource.push({
        ...customer_addresses[customer_address],
        key: customer_address
      });
    });


    return (
      <>
        <ModalAddAddress
          visible={this.state.visibleAdd}
          handleModal={this.handleModalAdd}
          submit={this.props.handleSubmitAddAddress}
          customer={this.props.customer}
          loading={loadingSubmit}
        />
        <ModalUpdateAddress
          visible={this.state.visibleUpdate}
          address_data={this.state.address_data}
          handleModal={this.handleModalUpdate}
          submit={this.props.handleSubmitUpdateAddress}
          customer={this.props.customer}
          loading={loadingSubmit}
        />

        <div
          style={{ marginRight: "20px", float: "right" }}
        >
          <Button
            type="primary"
            onClick={this.handleModalAdd.bind(this)}
          >
            <IntlMessages id="button.addAddress" />
          </Button>
        </div>


        {this.props.customer.loading ? (
          <div className="spinStyle">
            <Spin size="large" />
          </div>
        ) : (

            <CardWrapper>
              <Col span={24} style={{ marginTop: "20px", padding: "0 20px 0 20px" }}>
                <div className="isoInvoiceTable">
                  <Scrollbars style={{ width: "100%" }}>
                    <TableWrapper
                      // size="small"
                      dataSource={dataSource}
                      columns={columns}
                      loading={loading}
                      className="invoiceListTable"
                      bordered={true}
                      pagination={true}
                    />
                  </Scrollbars>
                </div>
              </Col>
            </CardWrapper>

          )}
      </>
    );
  }
}

const mapStateToProps = state => {
  return {
    customer: state.customer,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CustomerAddress);
