import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import moment from "moment";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/customer/customer/actions";
import { Form, Input, InputNumber, Row, Col, Select, Spin, Alert, Empty, Radio } from "antd";
import { getCurrencyRate } from "../../../../helpers/helper";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;


class CustomerUpdateProfileForm extends Component {

  state = {
    //check_code:false,
    currency_is_base: 3,
  }

  handleChangePaymentCategory = () => {
    const { setFieldsValue } = this.props.form
    setFieldsValue({
      id_payment_term: null
    })
  }

  setFieldRate = (id_currency, date) => {
    const { currencies } = this.props.customer
    const { setFieldsValue } = this.props.form

    currencies.map(data => {
      if (data.id_currency === id_currency) {
        this.setState({
          currency_is_base: data.is_base,
        });

        let rate = getCurrencyRate(data.currency_rate, date)
        setFieldsValue({
          ob_rate: rate
        })
      }
    })
  }

  handleChangeStartDate = (value) => {
    const { getFieldValue } = this.props.form
    if (getFieldValue('id_currency') && value instanceof moment) {
      this.setFieldRate(getFieldValue('id_currency'), value)
    }
  }

  handleChangeCurrency = (value) => {
    const { getFieldValue } = this.props.form
    this.setFieldRate(value, getFieldValue('start_date'))
  }


  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, value) => {
      if (!err) {
        const { updateCustomer } = this.props;
        const { id } = this.props.id_customer;
        // value.start_date = value.start_date.format("YYYY-MM-DD");
        value.discount = value.discount / 100;
        updateCustomer(id, value);
      }
    });


  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError, getFieldValue } = this.props.form;
    const { customer } = this.props;
    const { loading, loadingSubmit, payment_categories } = customer;
    const data = customer.customer ? customer.customer : [];

    const used = data.is_used;
    const error = customer.errorField ? customer.errorField : [];
    //console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const radioGroupStyle = {
      display: "flex",
      width: "100%"
    }

    const radioButtonStyle = {
      flex: "1",
      textAlign: "center"
    }

    return (
      <div>
        {this.props.customer.loadingEdit ? (
          <div className="spinStyle">
            <Spin size="large" />
          </div>
        ) : (

            <Form layout="vertical" onSubmit={this.handleSubmit}>
              {used ? (
                <Row style={{ marginBottom: "20px" }}>
                  <Col span={24}>
                    <Alert
                      message={<IntlMessages id="customer.help" />}
                      type="warning"
                      showIcon
                    />
                  </Col>
                </Row>
              ) : (
                  ""
                )}
              <div className="isoDetailDataFlex" style={{ padding: "0" }}>
                <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>

                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="customerTypes.pageHeader" />}
                  >
                    {getFieldDecorator("id_customer_type", {
                      initialValue: data.id_customer_type,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        loading={this.props.customer.loading}
                        showSearch
                        optionFilterProp="children"
                      //disabled={used ? true : false}
                      >
                        {this.props.customer.customer_types.map((data, key) => (
                          <Option key={key} value={data.id_customer_type}>
                            {data.customer_type_code + " (" + data.customer_type_name + ")"}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>

                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="customerTypes.add.code" />}
                    validateStatus={
                      error.customer_code || getFieldError("customer_code")
                        ? "error"
                        : null
                    }
                    help={
                      error.customer_code
                        ? error.customer_code
                        : getFieldError("customer_code")
                    }
                  >
                    {getFieldDecorator("customer_code", {
                      initialValue: data.customer_code,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        },
                        {
                          max: 30,
                          message: <IntlMessages id="maxLength30" />
                        }
                      ]
                    })(
                      <Input
                        disabled={used ? true : false}
                      //   style={{ textTransform: "uppercase" }}
                      />
                    )}
                  </Form.Item>


                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="customers.add.name" />}
                  >
                    {getFieldDecorator("customer_name", {
                      initialValue: data.customer_name,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        },
                        {
                          max: 50,
                          message: <IntlMessages id="maxLength50" />
                        }
                      ]
                    })(
                      <Input
                        disabled={used ? true : false}
                      />
                    )}
                  </Form.Item>

                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="customers.filter.registeredAt" />}
                  >
                    {getFieldDecorator("id_location", {
                      initialValue: data.id_location,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        loading={this.props.customer.loading}
                        showSearch
                        optionFilterProp="children"
                        disabled={used ? true : false}
                      >
                        {this.props.customer.locations.map((data, key) => (
                          <Option key={key} value={data.id_location}>
                            {data.location_code + " (" + data.location_name + ")"}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>

                  <InputGroup>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="paymentTerms.add.discount" />}
                      >
                        {getFieldDecorator("discount", {
                          initialValue: data.discount ? data.discount * 100 : 0,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            },
                          ]
                        })(
                          <InputNumber
                            //disabled={used ? true : false}
                            min={0}
                            max={100}
                            formatter={value => `${value}%`}
                            parser={value => value.replace('%', '')}
                            style={{ width: "100%" }}
                          />
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="customers.default" />}
                      >
                        {getFieldDecorator("is_default", {
                          initialValue: data.is_default ? data.is_default : 0,
                        })(
                          <Radio.Group
                            style={radioGroupStyle}
                            buttonStyle="solid"
                          >
                            <Radio.Button style={radioButtonStyle} value={1}><IntlMessages id="yes" /></Radio.Button>
                            <Radio.Button style={radioButtonStyle} value={0}><IntlMessages id="no" /></Radio.Button>
                          </Radio.Group>
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>

                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="customers.filter.employee" />}
                  >
                    {getFieldDecorator("id_related_employee", {
                      initialValue: data.id_related_employee,
                      // rules: [
                      //   {
                      //     required: true,
                      //     message: <IntlMessages id="required" />
                      //   }
                      // ]
                    })(
                      <Select
                        loading={this.props.customer.loading}
                        showSearch
                        optionFilterProp="children"
                        allowClear={true}
                      //disabled={used ? true : false}
                      >
                        {this.props.customer.employees.map((data, key) => (
                          <Option key={key} value={data.id_employee}>
                            {data.employee_code + " (" + data.first_name + (data.last_name ? " " + data.last_name : "") + ")"}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>

                </div>
                <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>

                  <InputGroup compact>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="currency" />}
                      >
                        {getFieldDecorator("id_currency", {
                          initialValue: data.id_currency,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })
                          (<Select
                            loading={this.props.customer.loading}
                            showSearch
                            optionFilterProp="children"
                            style={{ width: "100%" }}
                            onChange={this.handleChangeCurrency}
                            disabled={true}
                          >

                            {this.props.customer.currencies.map((data, key) => (
                              <Option key={key} value={data.id_currency}>
                                {data.currency_code + " (" + data.currency_name + ")"}
                              </Option>
                            ))}
                          </Select>)
                        }

                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="customers.add.creditLimit" />}
                      >
                        {getFieldDecorator("credit_limit", {
                          initialValue: data.credit_limit,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <InputNumber
                            formatter={value =>
                              `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                            }
                            parser={value => value.replace(/\$\s?|(,*)/g, "")}
                            min={0}
                            //disabled={used ? true : false}
                            style={{ width: "100%" }}
                            precision={2}
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>


                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="customers.filter.tax" />}
                  >
                    {getFieldDecorator("id_tax", {
                      initialValue: data.id_tax,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        loading={this.props.customer.loading}
                        showSearch
                        optionFilterProp="children"
                      //disabled={used ? true : false}
                      >
                        {this.props.customer.taxes.map((data, key) => (
                          <Option key={key} value={data.id_tax}>
                            {data.tax_name}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="customers.add.taxNumber" />}
                  >
                    {getFieldDecorator("tax_number", {
                      initialValue: data.tax_number,
                      rules: [
                        {
                          max: 25,
                          message: <IntlMessages id="maxLength25" />
                        },
                        // {
                        //   pattern: "^[0-9]*$",
                        //   message: <IntlMessages id="taxMustNumber" />
                        // }
                      ]
                    })(
                      <Input
                      //disabled={used ? true : false}
                      //addonBefore={is_cash}
                      //style={{ textTransform: "uppercase" }}
                      />
                    )}
                  </Form.Item>

                  <InputGroup compact>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="customers.filter.paymentCategory" />}
                      >
                        {getFieldDecorator("id_payment_category", {
                          initialValue: data.id_payment_category,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            loading={this.props.customer.loading}
                            showSearch
                            optionFilterProp="children"
                            //disabled={used ? true : false}
                            onChange={this.handleChangePaymentCategory}
                            style={{ width: "100%" }}
                          >
                            {this.props.customer.payment_categories.map((data, key) => (
                              <Option key={key} value={data.id_payment_category}>
                                {data.payment_category_code}
                              </Option>
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="customers.filter.paymentTerm" />}
                      >
                        {getFieldDecorator("id_payment_term", {
                          initialValue: data.id_payment_term,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            loading={this.props.customer.loading}
                            showSearch
                            optionFilterProp="children"
                            //disabled={used ? true : false}
                            notFoundContent={
                              loading
                                ?
                                (
                                  <Spin size="small" />
                                )
                                : getFieldValue('id_payment_category')
                                  ?
                                  (
                                    <Empty description={<IntlMessages id="noData" />} />
                                  )
                                  :
                                  (
                                    <Empty
                                      description={
                                        <IntlMessages id="selectPaymentCategoryFirst" />
                                      }
                                    />
                                  )
                            }
                            style={{ width: "100%" }}
                          >
                            {getFieldValue('id_payment_category')
                              ?
                              payment_categories.find((cate) =>
                                cate.id_payment_category === getFieldValue('id_payment_category')
                              ).payment_type === 2
                                ?
                                (
                                  this.props.customer.payment_terms.map((data, key) => (
                                    data.is_cash === 0
                                      ?
                                      (
                                        <Option key={key} value={data.id_payment_term}>
                                          {data.payment_term_code}
                                        </Option>
                                      )
                                      : null
                                  ))
                                )
                                :
                                (
                                  this.props.customer.payment_terms.map((data, key) => (
                                    data.is_cash === 1
                                      ?
                                      (
                                        <Option key={key} value={data.id_payment_term}>
                                          {data.payment_term_code}
                                        </Option>
                                      )
                                      : null
                                  ))
                                )
                              : null
                            }
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>

                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="customers.filter.accountReceivable" />}
                  >
                    {getFieldDecorator("id_account", {
                      initialValue: data.id_account,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        loading={this.props.customer.loading}
                        showSearch
                        optionFilterProp="children"
                      //disabled={used ? true : false}
                      // notFoundContent={
                      //   loading
                      //     ?
                      //     (<Spin size="small" />)
                      //     : getFieldValue('id_currency')
                      //       ?
                      //       (<Empty description={<IntlMessages id="noData" />} />)
                      //       :
                      //       (<Empty description={<IntlMessages id="select.currencyFirst" />} />)
                      // }
                      >
                        {this.props.customer.accounts.map((data, key) => (
                          // data.id_account_type === "03" && getFieldValue("id_currency") === data.id_currency
                          data.id_account_type === "03"
                            ? (<Option key={key} value={data.id_account}>
                              {data.account_code + " (" + data.account_name + ")"}
                            </Option>)
                            : null
                        ))}
                      </Select>
                    )}
                  </Form.Item>

                  <Form.Item
                    {...formTextArea}
                    label={<IntlMessages id="customers.add.notes" />}
                  >
                    {getFieldDecorator("notes", {
                      initialValue: data.notes
                    })(<TextArea autosize={{ minRows: 1 }} />)}
                  </Form.Item>

                </div>
              </div>

              <Row>
                <Col>
                  <Form.Item {...tailFormItemLayout}>
                    <Button
                      type="primary"
                      htmlType="submit"
                      style={{ marginTop: "20px", marginLeft: "50px" }}
                      loading={loading || loadingSubmit}
                    >
                      <IntlMessages id="button.submit" />
                    </Button>
                  </Form.Item>
                </Col>
              </Row>


            </Form>
          )}
      </div>
    );
  }
}

const WrappedCustomerUpdateProfileForm = Form.create({
  name: "update_customer"
})(CustomerUpdateProfileForm);

const mapStateToProps = state => {
  return { customer: state.customer };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(WrappedCustomerUpdateProfileForm);
