import React, { Component } from "react";
import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Modal, InputNumber, Col, DatePicker } from "antd";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;

class ModalAddOpeningBalance extends Component {

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        values.transaction_date = values.transaction_date.format('YYYY-MM-DD HH:mm:ss')
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { locations, currencies } = this.props.customer;
    const { loading } = this.props;
    const used = 0
    // const error = pettyCashType.errorField ? pettyCashType.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="button.addOpeningBalance" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="60%"
        okText={<IntlMessages id="button.submit"></IntlMessages>}
        cancelText={<IntlMessages id="button.cancel"></IntlMessages>}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <InputGroup>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="locations.pageHeader" />}
              >
                {getFieldDecorator("id_location", {
                  //initialValue: ,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.customer.loading}
                    showSearch
                    optionFilterProp="children"
                    style={{ width:"100%" }}
                  >
                    {locations.map((data, key) => (
                      <Option key={key} value={data.id_location}>
                        {data.location_code + " (" + data.location_name + ")"}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>
          </InputGroup>
          <InputGroup compact>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="currency" />}
              >
                {getFieldDecorator("id_currency", {
                    initialValue: this.props.customer.customer 
                    ? this.props.customer.customer.id_currency
                    : null,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })
                  (<Select
                    loading={this.props.customer.loading}
                    showSearch
                    optionFilterProp="children"
                    style={{ width: "100%" }}
                    //onChange={this.handleChangeCurrency}
                    disabled={true}
                  >
                  
                    {currencies.map((data, key) => (
                      <Option key={key} value={data.id_currency}>
                        {data.currency_code + " (" + data.currency_name +")"}
                      </Option>
                    ))}
                  </Select>)  
                }
                
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="rate" />}
              >
                {getFieldDecorator("ob_rate", {
                  initialValue: currencies.find((x) => x.id_currency === getFieldValue('id_currency'))
                  ?
                    currencies.find((x) => x.id_currency === getFieldValue('id_currency')).is_base === 1
                    ? 1 
                    : null
                  : null,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <InputNumber
                    formatter={value =>
                      `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                    }
                    parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    min={0.01}
                    disabled={
                      used || 
                      (
                        currencies.find((x) => x.id_currency === getFieldValue('id_currency')) &&
                        currencies.find((x) => x.id_currency === getFieldValue('id_currency')).is_base === 1
                      )
                        ? true 
                        : false}
                    style={{ width: "100%" }}
                    precision={2}
                  />
                )}
              </Form.Item>
            </Col>
          </InputGroup>
          <InputGroup compact>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="amount" />}
              >
                {getFieldDecorator("total_amount", {
                  //initialValue: data.total_amount,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <InputNumber
                    formatter={value =>
                      `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                    }
                    parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    //min={0}
                    disabled={used ? true : false}
                    style={{ width: "100%" }}
                    precision={2}
                  />
                )}
              </Form.Item>
            </Col>
            
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="transactionDate" />}
              >
                {getFieldDecorator("transaction_date", {
                  // initialValue: data.transaction_date ? moment(data.transaction_date, "YYYY-MM-DD HH:mm:ss") : null,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <DatePicker
                    //onChange={this.handleChangeTransactionDate}
                    style={{ width: "100%" }}
                    allowClear={false}
                    disabled={used ? true : false}
                    showTime={{ format: "HH:mm:ss" }}
                    format="DD MMMM YYYY HH:mm:ss"
                    disabledDate={e => e.valueOf() > moment()}
                  />
                )}
              </Form.Item>
            </Col>
          </InputGroup>

          <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="salesInvoice.no" />}
                >
                  {getFieldDecorator("sales_invoice_no", {
                    //initialValue: data.sales_invoice_no,
                    rules: [
                      {
                        max: 30,
                        message: <IntlMessages id="maxLength30" />
                      },
                    ]
                  })(
                    <Input
                      disabled={used ? true : false}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="salesOrders.salesOrderNo" />}
                >
                  {getFieldDecorator("sales_order_no", {
                    //initialValue: data.sales_order_no,
                    rules: [
                      {
                        max: 30,
                        message: <IntlMessages id="maxLength30" />
                      },
                    ]
                  })(
                    <Input
                      disabled={used ? true : false}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
              
        </Form>
      </Modal>
    );
  }
}

const WrappedModalAddOpeningBalance = Form.create({ name: "modal_add_address" })(
  ModalAddOpeningBalance
);

export default WrappedModalAddOpeningBalance;
