import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import { Tooltip, Col, Spin } from "antd";

import ModalAddContact from "./modalAddContact";
import ModalUpdateContact from "./modalUpdateContact";

import Popconfirms from "../../../../components/feedback/popconfirm";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/customer/customer/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange"

class CustomerContact extends Component {

  state = {
    visibleAdd: false,
    visibleUpdate: false,
    contact_data: null,
  }


  handleModalAdd = () => {
    this.setState({
      visibleAdd: !this.state.visibleAdd
    })
  }

  handleModalUpdate = row => {
    this.state.visibleUpdate
      ? this.setState({ visibleUpdate: false, contact_data: {} })
      : this.setState({ visibleUpdate: true, contact_data: row });
  }

  handleDelete = (id) => {
    let values = {};
    values.id_customer_contact = id;
    values.is_active = 0;
    this.props.handleSubmitUpdateContact(values)
  }

  listAction = {
    title: "",
    dataIndex: "id_customer_contact",
    rowKey: "id_customer_contact",
    width: "10%",
    align: "center",
    render: (text, row) => (
      <React.Fragment>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Button
            size="small"
            className="invoiceEdtBtn"
            onClick={() => this.handleModalUpdate(row)}
          >
            <i className="ion-android-create" />
          </Button>
        </Tooltip>
        <Popconfirms
          title={<IntlMessages id="confirmDelete" />}
          onConfirm={this.handleDelete.bind(this, text)}
          okText={<IntlMessages id="yes" />}
          cancelText={<IntlMessages id="no" />}
        >
          <Tooltip
            title={<IntlMessages id="delete" />}
          >
            <Button
              className="invoiceDltBtn"
              size="small"
            //style={{ marginRight:"10px" }}

            >
              <i className="ion-trash-b" />
            </Button>
          </Tooltip>
        </Popconfirms>
      </React.Fragment>
    )
  };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "customerContacts.add.name" }),
        dataIndex: "customer_contact_name",
        rowKey: "customer_contact_name",
        width: "25%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "employees.filter.jobTitles" }),
        dataIndex: "job_title",
        rowKey: "job_title",
        width: "25%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "locations.add.phone" }),
        dataIndex: "phone",
        rowKey: "phone",
        width: "25%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "locations.add.email" }),
        dataIndex: "email",
        rowKey: "email",
        width: "25%",
        render: text => <span>{text}</span>
      },
    ];

    // Data setup
    const { customer, loading, loadingSubmit } = this.props.customer;
    //console.log(customer)
    const customer_contacts = customer !== null ? customer.customer_contacts : [];
    //console.log(this.props.customer); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (
      permissions.includes("Update Customer") &&
      columns.length === 4
    ) {
      columns.push(this.listAction);
    }

    Object.keys(customer_contacts).map((customer_contact, index) => {
      return dataSource.push({
        ...customer_contacts[customer_contact],
        key: customer_contact
      });
    });

    return (
      <>
        <ModalAddContact
          visible={this.state.visibleAdd}
          handleModal={this.handleModalAdd}
          submit={this.props.handleSubmitAddContact}
          loading={loadingSubmit}
        />
        <ModalUpdateContact
          visible={this.state.visibleUpdate}
          contact_data={this.state.contact_data}
          handleModal={this.handleModalUpdate}
          submit={this.props.handleSubmitUpdateContact}
          loading={loadingSubmit}
        />

        <div
          style={{ marginRight: "20px", float: "right" }}
        >
          <Button
            type="primary"
            onClick={this.handleModalAdd.bind(this)}
          >
            <IntlMessages id="button.addContact" />
          </Button>
        </div>


        {this.props.customer.loading ? (
          <div className="spinStyle">
            <Spin size="large" />
          </div>
        ) : (

            <CardWrapper>
              <Col span={24} style={{ marginTop: "20px", padding: "0 20px 0 20px" }}>
                <div className="isoInvoiceTable">
                  <Scrollbars style={{ width: "100%" }}>
                    <TableWrapper
                      // size="small"
                      dataSource={dataSource}
                      columns={columns}
                      loading={loading}
                      className="invoiceListTable"
                      bordered={true}
                      pagination={true}
                    />
                  </Scrollbars>
                </div>
              </Col>
            </CardWrapper>

          )}
      </>
    );
  }
}

const mapStateToProps = state => {
  return {
    customer: state.customer,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CustomerContact);
