const actions = {
  getCustomers: (page, search, filter) => ({
    type: "GET_CUSTOMERS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createCustomer: () => ({
    type: "CREATE_CUSTOMER_REQUEST"
  }),
  storeCustomer: data => ({
    type: "STORE_CUSTOMER_REQUEST",
    payload: { body: data }
  }),
  storeCustomerContact: data => ({
    type: "STORE_CUSTOMER_CONTACT_REQUEST",
    payload: { body: data }
  }),
  storeCustomerAddress: data => ({
    type: "STORE_CUSTOMER_ADDRESS_REQUEST",
    payload: { body: data }
  }),
  storeCustomerOpeningBalance: data => ({
    type: "STORE_CUSTOMER_OPENING_BALANCE_REQUEST",
    payload: { body: data }
  }),
  editCustomer: id_customer => ({
    type: "EDIT_CUSTOMER_REQUEST",
    payload: { id_customer: id_customer }
  }),
  updateCustomer: (id_customer, data) => ({
    type: "UPDATE_CUSTOMER_REQUEST",
    payload: {
      id_customer: id_customer,
      body: data
    }
  }),
  updateCustomerContact: (id_customer_contact, data) => ({
    type: "UPDATE_CUSTOMER_CONTACT_REQUEST",
    payload: {
      id_customer_contact: id_customer_contact,
      body: data
    }
  }),
  updateCustomerAddress: (id_customer_address, data) => ({
    type: "UPDATE_CUSTOMER_ADDRESS_REQUEST",
    payload: {
      id_customer_address: id_customer_address,
      body: data
    }
  }),
  updateCustomerOpeningBalance: (id_customer_opening_balance, data) => ({
    type: "UPDATE_CUSTOMER_OPENING_BALANCE_REQUEST",
    payload: {
      id_customer_opening_balance,
      body: data
    }
  }),
  showCustomer: id_customer => ({
    type: "SHOW_CUSTOMER_REQUEST",
    payload: { id_customer: id_customer }
  }),
  deactivateCustomer: id_customer => ({
    type: "DEACTIVATE_CUSTOMER_REQUEST",
    payload: { id_customer }
  }),
  deactivateCustomerContact: id_customer_contact => ({
    type: "DEACTIVATE_CUSTOMER_CONTACT_REQUEST",
    payload: { id_customer_contact }
  }),
  deactivateCustomerAddress: id_customer_address => ({
    type: "DEACTIVATE_CUSTOMER_ADDRESS_REQUEST",
    payload: { id_customer_address }
  }),
  activateCustomer: id_customer => ({
    type: "ACTIVATE_CUSTOMER_REQUEST",
    payload: { id_customer }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  }),
  storeFranchise: data => ({
    type: "STORE_FRANCHISE_REQUEST",
    payload: {
      body: data
    }
  }),
  checkFranchiseConnection: url => ({
    type: "CHECK_FRANCHISE_CONNECTION_REQUEST",
    payload: {
      url: url
    }
  }),
  updateFranchise: (id_franchise_connection, data) => ({
    type: "UPDATE_FRANCHISE_REQUEST",
    payload: {
      id_franchise_connection: id_franchise_connection,
      body: data
    }
  }),
};

export default actions;
