import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import moment from "moment";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/customer/customer/actions";
// import notification from "../../../../components/notification";
import { Form, Input, DatePicker,Row, Col, 
  // Select, 
  Spin , Radio} from "antd";


// const { Option } = Select;
//const { TextArea } = Input;
//const InputGroup = Input.Group;


class CustomerUpdateCRMForm extends Component {
  

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, value) => {
      if (!err) {
        const { updateCustomer } = this.props;
        const { id } = this.props.id_customer;
        if(value.birth_date!=null){
          value.birth_date = value.birth_date.format("YYYY-MM-DD");
        }
        else{
          value.birth_date=null
        }
        updateCustomer(id, value);
      }
    });

    
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator } = this.props.form;
    const { customer } = this.props;
    const { loading, loadingSubmit } = customer;
    const data = customer.customer ? customer.customer : [];

    // const used = data.is_used;
    // const error = customer.errorField ? customer.errorField : [];
    //console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const radioGroupStyle = { 
      display: "flex", 
      width: "50%" 
    }

    const radioButtonStyle = { 
      flex: "1", 
      textAlign: "center" 
    }

    return (
      <div>
        {this.props.customer.loadingEdit  ? (
              <div className="spinStyle">
                <Spin size="large" />
              </div>
            ) : (

      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {/* {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="customer.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : (
          ""
        )} */}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="customers.add.cityBirthPlace" />}
            >
              {getFieldDecorator("city_birthplace", {
                initialValue: data.city_birthplace,
                // rules: [
                //   {
                //     required: true,
                //     message: <IntlMessages id="required" />
                //   }
                // ]
              })(
                // <Select
                //   loading={this.props.customer.loadingFilterCityBirthPlace}
                //   showSearch
                //   optionFilterProp="children"
                //   //disabled={used ? true : false}
                // >
                //   {this.props.customer.cities.map((data, key) => (
                //     <Option key={key} value={data.id_city}>
                //       {data.city_type + " " + data.city_name}
                //     </Option>
                //   ))}
                // </Select>
                <Input />
              )}
            </Form.Item>
            

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="customers.add.birthDate" />}
            >
              {getFieldDecorator("birth_date", {
                initialValue: data.birth_date ? moment(data.birth_date , "YYYY-MM-DD") : null
              })(
                <DatePicker
                  style={{ width: "100%" }}
                  allowClear={false}
                  //disabled={used ? true : false}
                  // showTime={{ format: "HH:mm:ss" }}
                  format="DD MMMM YYYY"
                  //disabledDate={e => e.valueOf() > moment()}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="gender" />}
            >
              {getFieldDecorator("gender", {
                initialValue: data.gender,
                // rules: [
                //   {
                //     required: true,
                //     message: <IntlMessages id="required" />
                //   }
                // ]
              })(
                <Radio.Group style={radioGroupStyle} buttonStyle="solid">
                  <Radio.Button style={radioButtonStyle} value={1}><IntlMessages id="Male" /></Radio.Button>
                  <Radio.Button style={radioButtonStyle} value={2}><IntlMessages id="Female" /></Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>       
                    
          </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="customers.add.religion" />}
            >
              {getFieldDecorator("religion", {
                initialValue: data.religion,
                // rules: [
                //   {
                //     required: true,
                //     message: <IntlMessages id="required" />
                //   }
                // ]
              })(<Input  
                
              />)}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="customers.add.jobTitle" />}
            >
              {getFieldDecorator("job_title", {
                initialValue: data.job_title,
                // rules: [
                //   {
                //     required: true,
                //     message: <IntlMessages id="required" />
                //   }
                // ]
              })(<Input  
                
              />)}
            </Form.Item>

          </div>
        </div>

        <Row>
          <Col>
          <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading ||loadingSubmit}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
          </Col>
        </Row>

        
      </Form>
      )}
    </div>
    );
  }
}

const WrappedCustomerUpdateCRMForm = Form.create({
  name: "update_customer_crm"
})(CustomerUpdateCRMForm);

const mapStateToProps = state => {
  return { customer: state.customer };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(WrappedCustomerUpdateCRMForm);
