import React, { Component } from "react";

//import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Modal, Col } from "antd";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;

class ModalAddAddress extends Component {

  handleChangeCountry = () => {
    const { setFieldsValue } = this.props.form;
    setFieldsValue({
      id_province: null,
      id_city:null,
      id_district: null
    })
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { loading } = this.props
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="addAddress" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="60%"
        okText={<IntlMessages id="button.submit"></IntlMessages>}
        cancelText={<IntlMessages id="button.cancel"></IntlMessages>}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <InputGroup>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="addressName" />}
              >
                {getFieldDecorator("customer_address_name", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 50,
                      message: <IntlMessages id="maxLength50" />
                    }
                  ]
                })(
                  <Input
                  />
                )}
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="zipCode" />}
              >
                {getFieldDecorator("zip_code", {
                  rules: [
                    {
                      max: 6,
                      message: <IntlMessages id="maxLength6" />
                    },
                    {
                      pattern: "[0-9]+",
                      message: <IntlMessages id="zipMustNumber" />
                    }
                  ]
                })(
                  <Input
                  />
                )}
              </Form.Item>
            </Col>
          </InputGroup>
          <InputGroup>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="address" />}
              >
                {getFieldDecorator("address", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <TextArea
                  />
                )}
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item {...formItemLayout} label={<IntlMessages id="phone1" />}>
                {getFieldDecorator("phone1", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 15,
                      message: <IntlMessages id="maxLength15" />
                    },
                    {
                      pattern: "^[0-9]*$",
                      message: <IntlMessages id="invalidPhone" />
                    }
                  ]
                })(
                  <Input
                  />
                )}
              </Form.Item>
            </Col>
          </InputGroup>
          <InputGroup>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="country" />}
              >
                {getFieldDecorator("id_country", {
                  initialValue: this.props.customer.default_id_country,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.customer.loading}
                    showSearch
                    optionFilterProp="children"
                    style={{ width:"100%" }}
                    onChange={this.handleChangeCountry}
                  >
                    {this.props.customer.countries.map((data, key) => (
                      <Option key={key} value={data.id_country}>
                        {data.country_name}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item {...formItemLayout} label={<IntlMessages id="phone2" />}>
                {getFieldDecorator("phone2", {
                  rules: [
                    {
                      max: 15,
                      message: <IntlMessages id="maxLength15" />
                    },
                    {
                      pattern: "^[0-9]*$",
                      message: <IntlMessages id="invalidPhone" />
                    }
                  ]
                })(
                  <Input
                  />
                )}
              </Form.Item>
            </Col>
          </InputGroup>
          <InputGroup>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="province" />}
              >
                {getFieldDecorator("id_province", {
                  
                })(
                  <Select
                    loading={this.props.customer.loading}
                    showSearch
                    optionFilterProp="children"
                    disabled={ getFieldValue('id_country') === "102" ? false : true }
                    style={{ width:"100%" }}
                  >
                    {
                      getFieldValue('id_country') === "102"
                      ?
                        this.props.customer.provinces.map((data, key) => (
                          <Option key={key} value={data.id_province}>
                            {data.province_name}
                          </Option>
                        ))
                      : null
                  }
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item {...formItemLayout} label={<IntlMessages id="fax" />}>
                {getFieldDecorator("fax_number", {
                  rules: [
                    {
                      max: 15,
                      message: <IntlMessages id="maxLength15" />
                    },
                    {
                      pattern: "^[0-9]*$",
                      message: <IntlMessages id="invalidFax" />
                    }
                  ]
                })(
                  <Input
                  
                  />
                )}
              </Form.Item>
            </Col>
          </InputGroup>
          <InputGroup>
            <Col span={12}>
              <Form.Item {...formItemLayout} label={<IntlMessages id="city" />}>
                {getFieldDecorator("id_city", {
                  
                })(
                  <Select
                    loading={this.props.customer.loading}
                    showSearch
                    optionFilterProp="children"
                    disabled={ getFieldValue('id_country') === "102" ? false : true }
                    style={{ width:"100%" }}
                  >
                    { 
                      this.props.customer.cities.map((data, key) => (
                        getFieldValue('id_province') === data.id_province
                        ? 
                          (
                            <Option key={key} value={data.id_city}>
                              {data.city_type + " " + data.city_name}
                            </Option>
                          )
                        : null
                      ))
                    }
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item {...formItemLayout} label={<IntlMessages id="email" />}>
                {getFieldDecorator("email", {
                  rules: [
                    {
                      type: "email",
                      message: <IntlMessages id="invalidEmail" />
                    }
                  ]
                })(<Input />)}
              </Form.Item>
              
            </Col>
          </InputGroup>
          <InputGroup>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="district" />}
              >
                {getFieldDecorator("id_district", {
                  
                })(
                  <Select
                    loading={this.props.customer.loading}
                    showSearch
                    optionFilterProp="children"
                    disabled={ getFieldValue('id_country') === "102" ? false : true }
                    style={{ width:"100%" }}
                  >
                    {this.props.customer.districts.map((data, key) => (
                      getFieldValue('id_city') === data.id_city
                      ?
                        (
                          <Option key={key} value={data.id_district}>
                            {data.district_name}
                          </Option>
                        )
                      : null
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="website" />}
              >
                {getFieldDecorator("website", {
                })(<Input />)}
              </Form.Item>
            </Col>
          </InputGroup>
              
        </Form>
      </Modal>
    );
  }
}

const WrappedModalAddAddress = Form.create({ name: "modal_add_address" })(
  ModalAddAddress
);

export default WrappedModalAddAddress;
