import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import CustomerUpdateProfileForm from "./updateProfileForm";
import CustomerUpdateCRMForm from "./updateCRMForm";
import CustomerContacts from "./indexContact";
import CustomerAddresses from "./indexAddress";
import CustomerOpeningBalances from "./indexOpeningBalance";
// import Franchise from "./indexFranchise";

import { Tabs, Spin } from "antd";

import notification from "../../../../components/notification";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Box } from "../../../App/app.style";
import actions from "../../../../redux/customer/customer/actions";

const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/setup/customers";

class UpdateCustomer extends Component {
  state = {
    id_customer: null,
    url: null
  };
  componentWillMount = () => {
    this.setState({
      id_customer: this.props.match.params,
      url: this.props.match.url
    });
  };

  componentDidMount = () => {
    const { editCustomer } = this.props;
    const { id } = this.props.match.params;
    editCustomer(id);
  };

  componentDidUpdate() {
    // Display error notif
    if (this.props.customer.error) {
      notification("error", this.props.customer.error);
      this.props.clearError();
    }
    if (this.props.customer.success) {
      notification("success", this.props.customer.success);
      this.props.clearSuccess();
    }
  }

  handleSubmit = value => {
    const { updateCustomer } = this.props;
    const { id } = this.props.match.params;
    updateCustomer(id, value);
  };

  handleSubmitAddContact = value => {
    const { storeCustomerContact } = this.props;
    const { id } = this.props.match.params;
    storeCustomerContact({ ...value, id_customer: id });
  };

  handleSubmitUpdateContact = value => {
    const { updateCustomerContact } = this.props;
    updateCustomerContact(value.id_customer_contact, value);
  };

  handleSubmitAddAddress = value => {
    const { storeCustomerAddress } = this.props;
    const { id } = this.props.match.params;
    storeCustomerAddress({ ...value, id_customer: id });
  };

  handleSubmitUpdateAddress = value => {
    const { updateCustomerAddress } = this.props;
    updateCustomerAddress(value.id_customer_address, value);
  };

  handleSubmitAddOpeningBalance = value => {
    const { storeCustomerOpeningBalance } = this.props;
    const { id } = this.props.match.params;
    storeCustomerOpeningBalance({ ...value, id_customer: id });
  };

  handleSubmitUpdateOpeningBalance = value => {
    const { updateCustomerOpeningBalance } = this.props;
    updateCustomerOpeningBalance(value.id_customer_opening_balance, value);
  };

  handleSubmitAddFranchise = value => {
    const { storeFranchise } = this.props;
    const { id } = this.props.match.params;
    const { customer_name } = this.props.customer.customer;
    storeFranchise({ ...value, id_entitas: id, entitas_name: customer_name });
  };

  handleSubmitUpdateFranchise = value => {
    const { updateFranchise } = this.props;
    const { id } = this.props.match.params;
    const { customer_name } = this.props.customer.customer;
    updateFranchise(value.id_franchise_connection, {
      ...value,
      id_entitas: id,
      entitas_name: customer_name
    });
  };

  render() {
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="customers.update.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="customers.tab.customer" />} key="1">
              {this.props.customer.loading ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <CustomerUpdateProfileForm
                  auth={this.props.auth}
                  id_customer={this.state.id_customer}
                  url={this.state.url}
                />
              )}
            </TabPane>
            <TabPane tab={<IntlMessages id="customers.tab.contact" />} key="2">
              {this.props.customer.loading ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <CustomerContacts
                  auth={this.props.auth}
                  handleSubmitAddContact={this.handleSubmitAddContact}
                  handleSubmitUpdateContact={this.handleSubmitUpdateContact}
                />
              )}
            </TabPane>
            <TabPane tab={<IntlMessages id="customers.tab.address" />} key="3">
              {this.props.customer.loading ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <CustomerAddresses
                  auth={this.props.auth}
                  handleSubmitAddAddress={this.handleSubmitAddAddress}
                  handleSubmitUpdateAddress={this.handleSubmitUpdateAddress}
                />
              )}
            </TabPane>
            <TabPane tab={<IntlMessages id="customers.tab.crm" />} key="4">
              {this.props.customer.loading ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <CustomerUpdateCRMForm
                  auth={this.props.auth}
                  id_customer={this.state.id_customer}
                  url={this.state.url}
                />
              )}
            </TabPane>
            <TabPane
              tab={<IntlMessages id="customers.tab.openingBalance" />}
              key="5"
            >
              {this.props.customer.loading ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <CustomerOpeningBalances
                  auth={this.props.auth}
                  handleSubmitAddOpeningBalance={
                    this.handleSubmitAddOpeningBalance
                  }
                  handleSubmitUpdateOpeningBalance={
                    this.handleSubmitUpdateOpeningBalance
                  }
                />
              )}
            </TabPane>
            {/* <TabPane
              tab={<IntlMessages id="franchise" />}
              key="6"
            >
              {this.props.customer.loading ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <Franchise
                  auth={this.props.auth}
                  handleSubmitAddFranchise={this.handleSubmitAddFranchise}
                  handleSubmitUpdateFranchise={this.handleSubmitUpdateFranchise}
                />
              )}
            </TabPane> */}
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { auth: state.auth, customer: state.customer };
};

export default compose(connect(mapStateToProps, actions))(UpdateCustomer);
