import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import moment from "moment";
import { Link } from "react-router-dom";
import ModalAddOpeningBalance from "./modalAddOpeningBalance";
import ModalUpdateOpeningBalance from "./modalUpdateOpeningBalance";
import NumberFormat from "../../../App/NumberFormat";

import { Tooltip, Spin, Col } from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
// import LayoutWrapper from "../../../../components/utility/layoutWrapper";
// import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/customer/customer/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  StatusBadge,
  StatusTag
} from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange"

// const Panel = Collapse.Panel;
// const Option = Select.Option;
// const Search = Input.Search;

const redirectSI = "/dashboard/sales/sales-invoices"

class CustomerOpeningBalance extends Component {

  state = {
    visibleAdd: false,
    visibleUpdate: false,
    opening_balance_data: null,
  }


  handleModalAdd = () => {
    this.setState({
      visibleAdd: !this.state.visibleAdd
    })
  }

  handleModalUpdate = row => {
    this.state.visibleUpdate
      ? this.setState({ visibleUpdate: false, opening_balance_data: {} })
      : this.setState({ visibleUpdate: true, opening_balance_data: row });
  }

  // handleDelete = (id) => {
  //   let values = {};
  //   values.id_customer_opening_balance = id;
  //   values.is_active = 0;
  //   this.props.handleSubmitUpdateOpeningBalance(values)
  // }


  listAction = {
    title: "",
    dataIndex: "id_customer_opening_balance",
    rowKey: "id_customer_opening_balance",
    width: "10%",
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <React.Fragment>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Button
            size="small"
            className="invoiceEdtBtn"
            onClick={() => this.handleModalUpdate(row)}
          //disabled={row.is_used === 1 ? true : false}
          >
            <i className="ion-android-create" />
          </Button>
        </Tooltip>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.SIdetail" />}
        >
          <Link to={`${redirectSI}/update/${row.id_sales_invoice}`}>
            <Button
              size="small"
              className="invoiceEdtBtn"
            >
              <i className="ion-android-open" />
            </Button>
          </Link>
        </Tooltip>
      </React.Fragment>
    )
  };

  render() {
    //console.log(this.props.customer);
    // console.log(this.state.filter);
    const columns = [
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "20%",

        render: (text, row) => {
          let className;
          switch (row.status) {
            case 1:
              className = "pending";
              break;
            case 2:
              className = "processed";
              break;
            case 3:
              className = "cancelled";
              break;
            default:
              className = "pending";
              break;
          }
          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
            </div>
          );
        }
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location.location_name",
        rowKey: "location.location_name",
        width: "15%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "salesInvoice.no" }),
        dataIndex: "sales_invoice_no",
        rowKey: "sales_invoice_no",
        width: "15%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "salesOrders.salesOrderNo" }),
        dataIndex: "sales_order_no",
        rowKey: "sales_order_no",
        width: "15%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "rateKurs" }),
        dataIndex: "ob_rate",
        rowKey: "ob_rate",
        width: "10%",
        align: "right",
        render: text => <NumberFormat value={parseFloat(text)} />

      },
      {
        title: getLanguage({ id: "amount" }),
        dataIndex: "total_amount",
        rowKey: "total_amount",
        width: "10%",
        align: "right",
        render: text => <NumberFormat value={parseFloat(text)} />

      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status",
        rowKey: "status",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "pending";
              display = getLanguage({ id: "pending" });
              break;
            case 2:
              className = "processed";
              display = getLanguage({ id: "processed" });
              break;
            case 3:
              className = "cancelled";
              display = getLanguage({ id: "cancelled" });
              break;
            default:
              className = "pending";
              display = getLanguage({ id: "pending" });
              break;
          }

          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }

    ];

    // Data setup
    const { customer, loading, loadingSubmit } = this.props.customer;
    //console.log(customer)
    const customer_opening_balances = customer !== null ? customer.customer_opening_balances : [];
    //console.log(this.props.customer); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (
      permissions.includes("Update Customer") &&
      columns.length === 7
    ) {
      columns.push(this.listAction);
    }

    Object.keys(customer_opening_balances).map((customer_opening_balance, index) => {
      return dataSource.push({
        ...customer_opening_balances[customer_opening_balance],
        key: customer_opening_balance
      });
    });


    return (
      <>
        <ModalAddOpeningBalance
          visible={this.state.visibleAdd}
          handleModal={this.handleModalAdd}
          submit={this.props.handleSubmitAddOpeningBalance}
          customer={this.props.customer}
          loading={loadingSubmit}
        />
        <ModalUpdateOpeningBalance
          visible={this.state.visibleUpdate}
          opening_balance_data={this.state.opening_balance_data}
          handleModal={this.handleModalUpdate}
          submit={this.props.handleSubmitUpdateOpeningBalance}
          customer={this.props.customer}
          loading={loadingSubmit}
        />

        <div
          style={{ marginRight: "20px", float: "right" }}
        >
          <Button
            type="primary"
            onClick={this.handleModalAdd.bind(this)}
          >
            <IntlMessages id="button.addOpeningBalance" />
          </Button>
        </div>


        {this.props.customer.loading ? (
          <div className="spinStyle">
            <Spin size="large" />
          </div>
        ) : (

            <CardWrapper>
              <Col span={24} style={{ marginTop: "20px", padding: "0 20px 0 20px" }}>
                <div className="isoInvoiceTable">
                  <Scrollbars style={{ width: "100%" }}>
                    <TableWrapper
                      // size="small"
                      dataSource={dataSource}
                      columns={columns}
                      loading={loading}
                      className="invoiceListTable"
                      bordered={true}
                      pagination={true}
                    />
                  </Scrollbars>
                </div>
              </Col>
            </CardWrapper>

          )}
      </>
    );
  }
}

const mapStateToProps = state => {
  return {
    customer: state.customer,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CustomerOpeningBalance);
